import { createSlice } from '@reduxjs/toolkit'

export const taskSlice = createSlice({
	name: 'task',
	initialState: {
		own: [],
		selected: null,
		edit: null
	},
	reducers: {
		wait: (state, action) => {
			state.own = null	
		},
		list: (state, action) => {
			state.own = action.payload
			state.selected = null
		},
		edit: (state, action) => {
			state.edit = action.payload
		},
		select: (state, action) => {
			state.selected = action.payload
		}
	}
})

export const { wait, list, edit, select } = taskSlice.actions;

export default taskSlice.reducer