import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { Container, Row, Col, ListGroup, Card, Badge, Accordion, Form, Button, Spinner } from 'react-bootstrap';
import { StatusBadge, StatusRange, Responsible } from './misc'
import { TaskStatusModal } from './taskstatusmodal'

import { getTask, deleteProject } from '../actions'


export function TaskDetails({task}) {
	
	const [edit, setEdit] = useState(null)
	
	function onClose() {
		setEdit(null);
	}
	
	return (
		<>
			<TaskStatusModal task={edit} onClose={() => setEdit(null)} />
			<Card className='mb-2'>
				<Card.Header>
					<Container>
						<Row>
							<Col className="me-auto">{task.name}</Col>
							<Col xs='auto'><StatusBadge task={task} /></Col>
						</Row>
					</Container>
				</Card.Header>
			<Card.Body>
				<TaskBody task={task} />
			</Card.Body>
			<Card.Footer>
				<Container>
					<Row>
						<Col className="ms-auto pe-0" md="auto"><Button size="sm" onClick={() => setEdit(task)}>Pivit tilanne</Button></Col>
					</Row>
				</Container>
			</Card.Footer>
			</Card>
		</>
	)
}

export function TaskBody({task}) {
	
	const labelstyle = {
		'fontSize': '10px',
		'fontWeight': '700',
		'margin': 'auto 0px'
	}

	const style = {
		fontSize: '10px',
		border: '1px solid #ced4da',
		borderRadius: '0.375rem',
		padding: '0.375rem 0.75rem',
		minHeight: '1.5rem'
	}
	
	return (
		<>
		<Row>
			<Col style={labelstyle} md={2}>Aloitus</Col><Col style={style}>{task.begin}</Col>
			<Col style={labelstyle} md="auto">Tavoiteaika</Col><Col style={style}>{task.deadline}</Col>
		</Row>
		<Row className="mt-2">
			<Col style={labelstyle} md={2}>Vastuuhenkil</Col><Col style={style}><Responsible task={task} /></Col>
		</Row>
		<Row className="mt-2"><Col style={labelstyle} md={2}>Tehtvn kuvaus</Col><Col style={style}>{task.description}</Col></Row>
		<Row className="mt-2"><Col style={labelstyle} md={2}>Valmiusaste</Col><Col><StatusRange task={task} /></Col></Row>
		<Row className="mt-2"><Col style={labelstyle} md={2}>Tilanne</Col><Col style={style}>{task.status}</Col></Row>
		{ task.tasks && <SubTasks tasks={task.tasks}  /> }
		</>
	)
}

export function OwnTasks() {
	
	const token = useSelector((state) => state.authorization.token)
	const dispatch = useDispatch()
	const tasks = useSelector((state) => state.task.own)
	const selected = useSelector((state) => state.task.selected)

	const namestyle = {
		overflow: 'hidden',
		whiteSpace: 'nowrap',
		textOverflow: 'ellipsis'
	}
	
	useEffect(() => {
		if(token !== null) {
			dispatch(getTask(token))
		}
	}, [token])


	if(tasks === null) {
		return <Spinner animation='grow' />
	} else {
		const tid = selected === null ? null : selected.projectId;
		return (
			<Card>
			<Card.Header>Omat tehtvt</Card.Header>
			<ListGroup variant='flush' className="mb-2">
				{ tasks.map(task =>  {
					const id = task.projectId;
					const tbsel = tid == id ? null : task;
					return (
						<ListGroup.Item action active={tid == id} key={id} onClick={() => dispatch({type: 'task/select', payload: tbsel})} style={namestyle}>
						<Container>
						<Row >
							<Col style={namestyle} className="me-auto">{task.name}</Col>
							<Col xs='auto'><StatusBadge task={task} /></Col>
						</Row>
						</Container>
						</ListGroup.Item>
					);
				})}
			</ListGroup>
			</Card>
		)
	}
}

export function TaskList({tasks}) {

	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)
	const [taskId, setTaskId] = useState(null);
	
	const itemstyle= {
		overflow: 'hidden',
		whiteSpace: 'nowrap',
		textOverflow: 'ellipsis',
		fontWeight: '700',
		fontSize: '12px'
	}
	
	if(tasks != null) {
	return (
	<Card>
		{ false && <Card.Header>Tehtvt</Card.Header> }
		<Accordion activeKey={taskId} onSelect={setTaskId}>
		{tasks.map(task => {
			return (
				<Accordion.Item key={task.projectId} eventKey={task.projectId}>
				<Accordion.Header ><div style={itemstyle} className="me-auto">{task.name}</div><StatusBadge task={task} /></Accordion.Header>
				<Accordion.Body>
					<TaskBody task={task} />
					<Row className="mt-2">
						<Col className="ms-auto pe-0" md="auto">
						<Button style={{fontSize: '0.7rem'}} size="sm" variant="danger" onClick={() => dispatch(deleteProject(token, task))}>Poista tehtv</Button>
						</Col>
						<Col className="pe-0" md="auto">
						<Button style={{fontSize: '0.7rem'}} size="sm" onClick={() => dispatch({type: 'task/edit', payload: task})}>Muokkaa</Button>
						</Col>
					</Row>
				</Accordion.Body>
				</Accordion.Item>
			)
		})}
		</Accordion>				
	</Card>
	)}
}
