
import { Container, Navbar, Nav, NavDropdown } from "react-bootstrap"
import { LinkContainer } from "react-router-bootstrap"
import { useDispatch, useSelector } from 'react-redux'

export function CompanyMenu({email}) {
	
	const dispatch = useDispatch()
	const language = useSelector((state) => state.authorization.language)
	const survey_licence = useSelector((state) => state.authorization.survey_licence)
	const project_licence = useSelector((state) => state.authorization.project_licence)
	
	const language_switch = language == 'fi' ? 'Vaihda englanniksi' : 'Switch to Finnish';
	const payload = { language: language == 'fi' ? 'en' : 'fi' };

	return (
	<Navbar collapseOnSelect expand="sm" bg="light">
        <Container>
          <Navbar.Brand href="https://www.hrci.io/">
            <img
              src="/hrcilogo.svg" width="86" height="39"
              className="d-inline-block align-top"
            />
          </Navbar.Brand>
          <Navbar.Toggle />
          <Navbar.Collapse>
          <Nav className="me-auto">
          	{ survey_licence && <LinkContainer to="/">
            <Nav.Link >Kysely</Nav.Link>
            </LinkContainer> } 
          	{ survey_licence && <LinkContainer to="/report">
            <Nav.Link>Tulokset</Nav.Link>
            </LinkContainer> }
          	{ project_licence && <LinkContainer to="/project">
            <Nav.Link>Projektit</Nav.Link>
            </LinkContainer> }
          </Nav>
          <Nav>
           	<NavDropdown align="end" title={email} id="userDropdown">
              <NavDropdown.Item onClick={() => dispatch({type: 'authorization/language', payload: payload })}>{language_switch}</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item onClick={() => dispatch({type: 'password/show'})}>Change Password</NavDropdown.Item>
              <NavDropdown.Divider />
    	      <NavDropdown.Item onClick={() => { window.location.replace("/"); } }>Log out</NavDropdown.Item>
            </NavDropdown>
          
          </Nav>
          </Navbar.Collapse>
        </Container>
    </Navbar>
	)

}